/* stylelint-disable */

:root {
	--blue: #3eb0ef;
	--green: #a4d037;
	--purple: #ad26b4;
	--yellow: #fecd35;
	--red: #f05230;
	--darkgrey: #15171a;
	--midgrey: #738a94;
	--lightgrey: #c5d2d9;
	--whitegrey: #e5eff5;
	--pink: #fa3a57;
	--brown: #a3821a;
	--darkmode: #1a1c20;
}

$nav-item-shadow:
	0 1px 8px rgba(0 0 0 / 50%),
	1px 0 8px rgba(0 0 0 / 50%),
	0 -1px 8px rgba(0 0 0 / 50%),
	-1px 0 8px rgba(0 0 0 / 50%);

a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
big,
blockquote,
body,
canvas,
caption,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
ul,
var,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font: inherit;
	font-size: 100%;
	vertical-align: baseline;
}

body {
	line-height: 1;
}

ol,
ul {
	list-style: none;
}

blockquote,
q {
	quotes: none;
}

blockquote::after,
blockquote::before,
q::after,
q::before {
	content: "";
	content: none;
}

img {
	max-width: 100%;
}

html {
	box-sizing: border-box;
	font-family: sans-serif;
	text-size-adjust: 100%;
}

*,
::after,
::before {
	box-sizing: inherit;
}

a {
	background-color: transparent;
}

a:active,
a:hover {
	outline: 0;
}

b,
strong {
	font-weight: 700;
}

dfn,
em,
i {
	font-style: italic;
}

h1 {
	margin: 0.67em 0;
	font-size: 2em;
}

small {
	font-size: 80%;
}

sub,
sup {
	position: relative;
	font-size: 75%;
	line-height: 0;
	vertical-align: baseline;
}

sup {
	top: -0.5em;
}

sub {
	bottom: -0.25em;
}

img {
	border: 0;
}

svg:not(:root) {
	overflow: hidden;
}

mark {
	background-color: #fdffb6;
}

code,
kbd,
pre,
samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
	margin: 0;
	color: inherit;
	font: inherit;
}

button {
	overflow: visible;
	border: none;
}

button,
select {
	text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
	cursor: pointer;
	appearance: button;
}

button[disabled],
html input[disabled] {
	cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
	padding: 0;
	border: 0;
}

input {
	line-height: normal;
}

input:focus {
	outline: none;
}

input[type="checkbox"],
input[type="radio"] {
	box-sizing: border-box;
	padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
	height: auto;
}

input[type="search"] {
	box-sizing: content-box;
	appearance: textfield;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
	appearance: none;
}

legend {
	padding: 0;
	border: 0;
}

textarea {
	overflow: auto;
}

table {
	border-spacing: 0;
	border-collapse: collapse;
}

td,
th {
	padding: 0;
}

html {
	overflow-y: scroll;
	font-size: 62.5%;
	-webkit-tap-highlight-color: rgb(0 0 0 / 0%);
}

body,
html {
	overflow-x: hidden;
}

body {
	color: #303a3e;
	font-family:
		-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu,
		Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	font-size: 1.6rem;
	line-height: 1.6em;
	font-weight: 400;
	font-style: normal;
	letter-spacing: 0;
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-feature-settings: "liga" on;
}

::selection {
	text-shadow: none;
	background: #cbeafb;
}

::selection {
	text-shadow: none;
	background: #cbeafb;
}

hr {
	position: relative;
	display: block;
	width: 100%;
	margin: 2.5em 0 3.5em;
	padding: 0;
	height: 1px;
	border: 0;
	border-top: 1px solid #e4eaed;
}

audio,
canvas,
iframe,
img,
svg,
video {
	vertical-align: middle;
}

fieldset {
	margin: 0;
	padding: 0;
	border: 0;
}

textarea {
	resize: vertical;
}

blockquote,
dl,
ol,
p,
ul {
	margin: 0 0 1.5em;
}

ol,
ul {
	padding-left: 1.3em;
	padding-right: 1.5em;
}

ol ol,
ol ul,
ul ol,
ul ul {
	margin: 0.5em 0 1em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

ol,
ul {
	max-width: 100%;
}

li {
	margin: 0.5em 0;
	padding-left: 0.3em;
	line-height: 1.6em;
}

dt {
	float: left;
	margin: 0 20px 0 0;
	width: 120px;
	color: var(--darkgrey);
	font-weight: 500;
	text-align: right;
}

dd {
	margin: 0 0 5px;
	text-align: left;
}

blockquote {
	margin: 1.5em 0;
	padding: 0 1.6em;
	border-left: 0.5em solid var(--whitegrey);
}

blockquote p {
	margin: 0.8em 0;
	font-size: 1.2em;
	font-weight: 300;
}

blockquote small {
	display: inline-block;
	margin: 0.8em 0 0.8em 1.5em;
	font-size: 0.9em;
	opacity: 0.8;
}

blockquote small::before {
	content: "\2014 \00A0";
}

blockquote cite {
	font-weight: 700;
}

blockquote cite a {
	font-weight: 400;
}

a {
	color: #26a6ed;
	text-decoration: none;
}

a:hover {
	text-decoration: underline;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin-top: 0;
	line-height: 1.15;
	font-weight: 600;
	text-rendering: optimizeLegibility;
}

h1 {
	margin: 0 0 0.5em;
	font-size: 5.5rem;
	font-weight: 600;
}
@media (width <= 500px) {
	h1 {
		font-size: 2.2rem;
	}
}

h2 {
	margin: 1.5em 0 0.5em;
	font-size: 2.2rem;
}
@media (width <= 500px) {
	h2 {
		font-size: 1.8rem;
	}
}

h3 {
	margin: 1.5em 0 0.5em;
	font-size: 1.8rem;
	font-weight: 500;
}
@media (width <= 500px) {
	h3 {
		font-size: 1.7rem;
	}
}

h4 {
	margin: 1.5em 0 0.5em;
	font-size: 1.6rem;
	font-weight: 500;
}

h5,
h6 {
	margin: 1.5em 0 0.5em;
	font-size: 1.4rem;
	font-weight: 500;
}

body {
	background: #fff;
}

.img {
	display: block;
	width: 100%;
	height: 100%;
	background-position: 50%;
	background-size: cover;
	border-radius: 100%;
}

.hidden {
	visibility: hidden;
	position: absolute;
	text-indent: -9999px;
}

.site-wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}

.site-main {
	z-index: 100;
	flex-grow: 1;
}

.outer {
	position: relative;
	padding: 0 5vw;
}

.inner {
	margin: 0 auto;
	max-width: 1040px;
	width: 100%;
}
@media (width >= 900px) {
	.author-template .post-feed,
	.home-template .post-feed,
	.tag-template .post-feed {
		padding: 40px 0 5vw;
		border-top-left-radius: 3px;
		border-top-right-radius: 3px;
	}

	.home-template .site-nav {
		position: relative;
	}
}

.site-header-background {
	position: relative;
	// margin-top: 64px;
	padding-bottom: 12px;
	color: #fff;
	background: #0a0b0c no-repeat 50%;
	background-size: cover;
}

.site-header-background::before {
	bottom: 0;
	background: rgb(0 0 0 / 18%);
}

.site-header-background::after,
.site-header-background::before {
	content: "";
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	z-index: 10;
	display: block;
}

.site-header-background::after {
	bottom: auto;
	height: 140px;
	background: linear-gradient(rgb(0 0 0 / 45%), transparent);
}

.site-header-background.no-image::after,
.site-header-background.no-image::before {
	display: none;
}

.site-header-content {
	z-index: 100;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 6vw 3vw;
	min-height: 200px;
	max-height: 340px;
}

.site-title {
	z-index: 10;
	margin: 0 0 0 -2px;
	padding: 0;
	font-size: 5rem;
	line-height: 1em;
	// font-weight: 600;
	text-shadow:
		0 1px 8px rgba(0 0 0 / 50%),
		1px 0 8px rgba(0 0 0 / 50%),
		0 -1px 8px rgba(0 0 0 / 50%),
		-1px 0 8px rgba(0 0 0 / 50%);
}

.site-logo {
	max-height: 55px;
}

.site-description {
	z-index: 10;
	margin: 0;
	padding: 5px 0;
	font-size: 2.5rem;
	line-height: 1.4em;
	// font-weight: bold;
	opacity: 1;
	text-shadow:
		0 1px 8px rgba(0 0 0 / 50%),
		1px 0 8px rgba(0 0 0 / 50%),
		0 -1px 8px rgba(0 0 0 / 50%),
		-1px 0 8px rgba(0 0 0 / 50%);
}

.site-home-header {
	z-index: 1000;
}

.site-home-header .site-header-background {
	margin-top: 0;
}

.site-home-header .site-header-content {
	padding: 5vw 3vw 6vw;
}

.site-home-header .site-title {
	font-size: 5.5rem;
	text-align: center;
}

.site-home-header .site-description {
	// font-size: 2.2rem;
	// font-weight: 300;
	text-align: center;
}

.site-archive-header .site-header-content {
	position: relative;
	align-items: stretch;
	padding: 12vw 0 20px;
	min-height: 200px;
	max-height: 600px;
}

.site-archive-header .no-image {
	padding-top: 0;
	padding-bottom: 0;
	color: var(--darkgrey);
	background: #fff;
	opacity: 1;
}

.site-archive-header .no-image .site-description {
	color: var(--midgrey);
	opacity: 1;
}

.site-archive-header .no-image .site-header-content {
	padding: 5vw 0 10px;
	border-bottom: 1px solid #eaeff1;
}
@media (width <= 900px) {
	.site-header-content {
		padding-bottom: 9vw;
	}
}
@media (width <= 500px) {
	.site-home-header .site-title {
		font-size: 4.2rem;
	}

	.site-home-header .site-description {
		font-size: 1.8rem;
	}

	.site-archive-header .site-header-content {
		flex-direction: column;
		align-items: center;
		min-height: unset;
	}

	.site-archive-header .site-title {
		font-size: 4.2rem;
		text-align: center;
	}

	.site-archive-header .no-image .site-header-content {
		padding: 12vw 0 20px;
	}
}

.site-nav-main {
	// position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 1000;
	background: #0a0b0c;
}

.site-nav {
	position: relative;
	z-index: 100;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	overflow: hidden;
	height: 64px;
	font-size: 1.3rem;
}

.site-nav-left-wrapper {
	position: relative;
	flex: 1 0 auto;
	display: flex;
}

.site-header-background:not(.responsive-header-img)
	.site-nav-left-wrapper::after,
.site-nav-main .site-nav-left-wrapper::after {
	content: "";
	position: absolute;
	top: 0;
	z-index: 1000;
	width: 40px;
	height: 100%;
	right: 0;
	background: linear-gradient(90deg, rgb(10 11 12 / 0%) 0, #0a0b0c);
}

.site-nav-left {
	flex: 1 0 auto;
	display: flex;
	align-items: center;
	overflow-x: auto;
	overflow-y: hidden;
	-webkit-overflow-scrolling: touch;
	margin-right: 10px;
	padding: 10px 0 80px;
	font-weight: 500;
	letter-spacing: 0.2px;
	text-transform: uppercase;
	white-space: nowrap;
	-ms-overflow-scrolling: touch;
}

.site-nav-left .nav li:last-of-type {
	padding-right: 20px;
}

.site-nav-logo {
	position: relative;
	z-index: 100;
	flex-shrink: 0;
	display: inline-block;
	margin-right: 32px;
	padding: 12px 0;
	color: #fff;
	font-size: 1.7rem;
	// line-height: 1.8rem;
	// font-weight: 700;
	letter-spacing: -0.5px;
	// text-transform: none;
}

.site-nav-logo:hover {
	text-decoration: none;
}

.site-nav-logo img {
	display: block;
	width: auto;
	height: 21px;
}

.site-home-header .site-nav-logo {
	display: none;
}

.site-nav-content {
	position: relative;
	align-self: flex-start;
}

.nav {
	position: absolute;
	z-index: 1000;
	display: flex;
	margin: 0 0 0 -12px;
	padding: 0;
	list-style: none;
	transition: all 1s cubic-bezier(0.19, 1, 0.22, 1);
}

.nav li {
	display: block;
	margin: 0;
	padding: 0;
	// font-weight: bold;
	font-size: 1.6rem;
	text-shadow: $nav-item-shadow;
}

.nav li a {
	position: relative;
	display: block;
	padding: 12px;
	color: #fff;
	opacity: 0.8;
	transition: opacity 0.35s ease-in-out;
}

.nav li a:hover {
	text-decoration: none;
	opacity: 1;
}

.nav li a::before {
	content: "";
	position: absolute;
	right: 100%;
	bottom: 8px;
	left: 12px;
	height: 1px;
	background: #fff;
	opacity: 0.25;
	transition: all 0.35s ease-in-out;
}

.nav li a:hover::before {
	right: 12px;
	opacity: 0.5;
}

.nav-post-title-active .nav {
	visibility: hidden;
	opacity: 0;
	transform: translateY(-175%);
}

.nav-post-title {
	visibility: hidden;
	position: absolute;
	top: 9px;
	color: #fff;
	font-size: 1.7rem;
	font-weight: 400;
	text-transform: none;
	opacity: 0;
	transition: all 1s cubic-bezier(0.19, 1, 0.22, 1);
	transform: translateY(175%);
}

.nav-post-title.dash {
	left: -25px;
}

.nav-post-title.dash::before {
	content: "â€“ ";
	opacity: 0.5;
}

.nav-post-title-active .nav-post-title {
	visibility: visible;
	opacity: 1;
	transform: translateY(0);
}

.site-nav-right {
	flex: 0 1 auto;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	padding: 10px 0;
	height: 64px;
}

.site-nav-right .nav {
	position: relative;
	margin: 0;
}

.site-nav-right .nav a {
	white-space: nowrap;
}

.site-nav-right .nav a::before {
	display: none;
}

.site-nav-right .nav li:last-of-type a {
	margin-right: -12px;
}

.social-links {
	flex-shrink: 0;
	display: flex;
	align-items: center;
}

.social-link {
	display: inline-block;
	margin: 0;
	padding: 10px;
	opacity: 0.8;
}

.social-link:hover {
	opacity: 1;
}

.social-link svg {
	height: 1.8rem;
	fill: #fff;
}

.social-link-fb svg,
.social-link-wb svg {
	height: 1.6rem;
}

.social-link-wb svg path {
	stroke: #fff;
}

.social-link-rss svg {
	height: 1.9rem;
}

.subscribe-button {
	display: block;
	margin: 0 0 0 10px;
	padding: 4px 10px;
	border: 1px solid #fff;
	color: #fff;
	line-height: 1em;
	border-radius: 10px;
	opacity: 0.8;
}

.subscribe-button:hover {
	text-decoration: none;
	opacity: 1;
}

.site-nav-right .nav + .subscribe-button {
	margin-left: 24px;
}

.rss-button {
	padding: 10px 8px;
	opacity: 0.8;
}

.rss-button:hover {
	opacity: 1;
}

.rss-button svg {
	margin-bottom: 1px;
	height: 2.1rem;
	fill: #fff;
}

.home-template .site-nav-main {
	z-index: 100;
}

.home-template .site-nav-main .site-nav {
	opacity: 0;
	transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1) 0s;
}

.home-template .site-nav-main .fixed-nav-active {
	opacity: 1;
	transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1) 0.05s;
}
@media (width <= 700px) {
	.site-home-header .site-nav {
		margin-left: -5vw;
	}

	.site-nav-main {
		padding-right: 0;
		padding-left: 0;
	}

	.site-nav-left {
		margin-right: 0;
		padding-left: 5vw;
	}

	.site-nav-right {
		display: none;
	}
}

.posts {
	overflow-x: hidden;
}

.post-feed {
	flex-wrap: wrap;
	margin: 0 -20px;
	padding: 50px 0 0;
	background: #fff;
}

.post-card,
.post-feed {
	position: relative;
	display: flex;
}

.post-card {
	flex: 1 1 301px;
	flex-direction: column;
	overflow: hidden;
	margin: 0 0 40px;
	padding: 0 20px 40px;
	min-height: 220px;
	border-bottom: 1px solid #eaeff1;
	background-size: cover;
}

.post-card-image-link {
	position: relative;
	display: block;
	overflow: hidden;
	border-radius: 3px;
}

.post-card-image {
	width: 100%;
	height: 200px;
	background: var(--lightgrey) no-repeat 50%;
	object-fit: cover;
}

.post-card-content-link {
	position: relative;
	display: block;
	color: var(--darkgrey);
}

.post-card-content-link:hover {
	text-decoration: none;
}

.post-card-header {
	margin: 15px 0 0;
}

.post-feed .no-image .post-card-content-link {
	padding: 0;
}

.no-image .post-card-header {
	margin-top: 0;
}

.post-card-primary-tag {
	margin: 0 0 0.2em;
	color: var(--blue);
	font-size: 1.4rem;
	font-weight: 500;
	letter-spacing: 0.2px;
	text-transform: uppercase;
}

.post-card-title {
	margin: 0 0 0.4em;
	line-height: 1.15em;
	transition: color 0.2s ease-in-out;
}

.no-image .post-card-title {
	margin-top: 0;
}

.post-card-content {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
}

.post-card-excerpt {
	max-width: 56em;
	color: var(--midgrey);
	font-family: Georgia, serif;
}

.post-card-excerpt p {
	margin-bottom: 1em;
}

.post-card-meta {
	display: flex;
	align-items: flex-start;
	padding: 0;
}

.author-profile-image,
.avatar-wrapper {
	display: block;
	width: 100%;
	height: 100%;
	background: #e4eaed;
	border-radius: 100%;
	object-fit: cover;
}

.post-card-meta .avatar-wrapper,
.post-card-meta .profile-image-wrapper {
	position: relative;
}

.author-list {
	display: flex;
	flex-wrap: wrap;
	margin: 0 0 0 4px;
	padding: 0;
	list-style: none;
}

.author-list-item {
	position: relative;
	flex-shrink: 0;
	margin: 0;
	padding: 0;
}

.static-avatar {
	display: block;
	overflow: hidden;
	margin: 0 0 0 -6px;
	width: 34px;
	height: 34px;
	border: 2px solid #fff;
	border-radius: 100%;
}

.author-name-tooltip {
	position: absolute;
	bottom: 105%;
	z-index: 999;
	display: block;
	padding: 2px 8px;
	color: #fff;
	font-size: 1.2rem;
	letter-spacing: 0.2px;
	white-space: nowrap;
	background: var(--darkgrey);
	border-radius: 3px;
	box-shadow:
		0 12px 26px rgb(39 44 49 / 8%),
		1px 3px 8px rgb(39 44 49 / 3%);
	opacity: 0;
	transition: all 0.35s cubic-bezier(0.4, 0.01, 0.165, 0.99);
	transform: translateY(6px);
	pointer-events: none;
}

.author-list-item:hover .author-name-tooltip {
	opacity: 1;
	transform: translateY(0);
}
@media (width <= 700px) {
	.author-name-tooltip {
		display: none;
