.gh-post-upgrade-cta-content,
.gh-post-upgrade-cta {
	display: flex;
	flex-direction: column;
	align-items: center;
	font-family:
		-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
		"Cantarell", "Open Sans", "Helvetica Neue", sans-serif;
	text-align: center;
	width: 100%;
	color: #fff;
	font-size: 16px;
}

.gh-post-upgrade-cta-content {
	border-radius: 8px;
	padding: 40px 4vw;
}

.gh-post-upgrade-cta h2 {
	color: #fff;
	font-size: 28px;
	letter-spacing: -0.2px;
	margin: 0;
	padding: 0;
}

.gh-post-upgrade-cta p {
	margin: 20px 0 0;
	padding: 0;
}

.gh-post-upgrade-cta small {
	font-size: 16px;
	letter-spacing: -0.2px;
}

.gh-post-upgrade-cta a {
	color: #fff;
	cursor: pointer;
	font-weight: 500;
	box-shadow: none;
	text-decoration: underline;
}

.gh-post-upgrade-cta a:hover {
	color: #fff;
	opacity: 0.8;
	box-shadow: none;
	text-decoration: underline;
}

.gh-post-upgrade-cta a.gh-btn {
	display: block;
	background: #fff;
	text-decoration: none;
	margin: 28px 0 0;
	padding: 8px 18px;
	border-radius: 4px;
	font-size: 16px;
	font-weight: 600;
}

.gh-post-upgrade-cta a.gh-btn:hover {
	opacity: 0.92;
}
